import React from "react";
import setActiveUserId from '../../../actions/setuserid'
import "./user.css";
import store from "../../../store";
const User = ({ contact }) => {
  function handleUserClick({user_id}) {
    store.dispatch(setActiveUserId({user_id}))
    
  }
  return (
    <div className="user" onClick={handleUserClick.bind(null, contact)}>
      <img className="userPic" alt="loading.." src={contact.profile_pic} />

      <div className="user_details">
        <p className="user_name">{contact.name}</p>
        <p className="user_status"> {contact.status}</p>
      </div>
    </div>
  );
};

export default User;
