import _ from 'lodash'
import {getMessages} from '../staticData'
export default (state=getMessages(10),action)=>{
   
    switch (action.type) {
        case 'SAVE_USR_MSG':
          const { msg, userId } = action.payload;
          const allUserMsgs = state[userId];
          const number = +_.keys(allUserMsgs).pop() + 1;
    
          return {
            ...state,
            [userId]: {
              ...allUserMsgs,
              [number]: {
                number,
                text: msg,
                is_user_msg: true
              }
            }
          };
    
        default:
          return state;
      }


}