import React,{Component} from 'react';
import './chatwidow.css'
import Header from './components/header/header'
import Chats from './components/chats/chats'
import InputArea from './components/inputarea/inputarea'
import store from '../../../store/index'
import _ from 'lodash'

class Chatwindow extends Component {
// constructor(props) {
//     super(props);
//   //  this.chatsRef = React.createRef();
   
//   }
  
  render() {
      const users=store.getState()
      const key=users.activeuserID.active_user_id.user_id;
      const profile=users.contacts.contacts[key];
      const messages=users.messages[key]
    return (<div className='chatwindow ' ref={this.chatsRef}>
            <Header profile={profile}/>
            <Chats messages={_.values(messages)} chatsRef={this.chatsRef} />
            <InputArea userid={profile.user_id}/>
        

        </div>);
  }

}

export default Chatwindow