import React from 'react';
import './sidebar.css';
import store from '../../store/index';
import User from './components/user';
import _ from 'lodash';

const Sidebar=()=>{
    const contacts=_.values(store.getState().contacts.contacts)
   
    
    return(
        <aside className='sidebar' >
            { contacts.map((contact)=>{return(<User contact={contact} key={contact.user_id}/>)})} 
        </aside>
    )
}
 
export default Sidebar